<template>
  <div class="commerceOrderList">
    <navigation-bar pageName="订单列表" right-text @onLeftClick="onClickLeft" />
    <div class="searchPanel">
      <div class="searchInput" @click="gotoSearch()">
        <img class="searchImg" :src="serachIcon" />
        搜索订单编号
      </div>
    </div>

    <div class="commerce-tabs-box">
      <van-tabs v-model="active" @change="onTabChange" color="#00d8d1" swipeable>
        <van-tab title="全部">
          <div v-if="orderList.length <=0" class="noMessagePanel">
            <div class="noMessage">
              <img :src="imgDefault" class="noMessageImg" />
              <p>暂无订单</p>
            </div>
          </div>
          <van-list
            v-if="orderList.length >0"
            v-model="isLoading"
            :finished="finished"
            finished-text="没有更多了"
            :immediate-check="false"
            @load="loadMore"
          >
            <div class="card van-clearfix" v-for="(orderInfo,index) in orderList" :key="index">

              <div class="orderTitlePanel" @click="gotoCommerceDetail(orderInfo)" v-if="orderInfo">
                <div class="orderTitleStore">{{orderInfo.shopName}}</div>
                <div class="orderCode">订单号：{{orderInfo.subBillNo}}</div>
                <div v-if="orderInfo.deliveryType==0" class="SendTypeDelivery">
                  <div class="sendType">快递配送</div>
                </div>
                <div v-if="orderInfo.deliveryType==1" class="SendTypeDelivery">
                  <div class="sendType">销售配送</div>
                </div>
                <div v-if="orderInfo.deliveryType==2" class="SendTypeSelf">
                  <div class="sendType">到店自提</div>
                </div>
              </div>
              <div class="van-hairline--top" v-if="orderInfo"></div>

              <div class="orderInfoPanel" @click="gotoCommerceDetail(orderInfo)" v-if="orderInfo">
                <van-image class="orderImg" lazy-load v-bind:src="orderInfo.firstGoodsPicUrl" />
                <div class="orderDetail">
                  <div style="display: flex;">
                    <div class="orderName userName">{{orderInfo.recName}} </div>
                    <div class="orderName" style="margin-left:10px;">{{orderInfo.recPhone}}</div>
                    <div class="orderStatus">{{orderInfo.billStatus}}</div>
                  </div>
                  <div class="orderName">{{orderInfo.recAddr}}</div>
                  <div class="orderName" style="color: #9a9a9a">{{orderInfo.createdTime}}</div>
                </div>
              </div>
              <div class="van-hairline--top lineMargin"></div>

              <div class="pricePanel" @click="gotoCommerceDetail(orderInfo)" v-if="orderInfo && orderInfo.goodsInfo && orderInfo.goodsInfo.length > 0">
                <div class="orderGoods">{{orderInfo.goodsInfo[0].goodsName}}</div>
                <div class="orderGoodsNum">共{{orderInfo.goodsInfo.length}}件商品</div>
                <div>
                  <span class="orderPrice">
                    <span style="font-size: 14px;">￥</span>
                    {{orderInfo.realPayTotal}}
                  </span>
                </div>
              </div>
              <div class="receivePanel" v-if="orderInfo.billStatus=='待收货' && orderInfo && orderInfo.goodsInfo && orderInfo.goodsInfo.length > 0">
                <div class="receiveBtn" @click="received(orderInfo)">已送达</div>
                <div class="receiveBtn" v-if="orderInfo.noticeTimes && orderInfo.noticeTimes <= 0" @click="goodArrive(orderInfo, index)">已接货</div>
                <div class="receiveBtn-disabled" v-else-if="orderInfo.noticeTimes && orderInfo.noticeTimes >= 1">已接货</div>
                <div class="receiveBtn" v-else @click="goodArrive(orderInfo, index)">已接货</div>
                <div class="receiveBtn" @click="getDadaExpressInfo(orderInfo)" v-if="dadaExpressSurported && orderInfo.expressCompany && (orderInfo.expressCompany.indexOf('达达') != -1)">查看物流</div>
              </div>
            </div>
          </van-list>
        </van-tab>
        <van-tab title="待收货">
          <div v-if="orderList.length <=0" class="noMessagePanel">
            <div class="noMessage">
              <img :src="imgDefault" style="display:block;width:2rem;margin: 0 auto;" />
              <p>暂无订单</p>
            </div>
          </div>
          <van-list
            v-if="orderList.length >0"
            v-model="isLoading"
            :finished="finished"
            finished-text="没有更多了"
            @load="loadMore"
            :immediate-check="false"
          >
            <div class="card" v-for="(orderInfo,index) in orderList" :key="index">

              <div class="orderTitlePanel" @click="gotoCommerceDetail(orderInfo)" v-if="orderInfo">
                <div class="orderTitleStore">{{orderInfo.shopName}}</div>
                <div class="orderCode">订单号：{{orderInfo.subBillNo}}</div>
                <div v-if="orderInfo.deliveryType==0" class="SendTypeDelivery">
                  <div class="sendType">快递配送</div>
                </div>
                <div v-if="orderInfo.deliveryType==1" class="SendTypeDelivery">
                  <div class="sendType">销售配送</div>
                </div>
                <div v-if="orderInfo.deliveryType==2" class="SendTypeSelf">
                  <div class="sendType">到店自提</div>
                </div>
              </div>
              <div class="van-hairline--top" v-if="orderInfo"></div>

              <div class="orderInfoPanel" @click="gotoCommerceDetail(orderInfo)" v-if="orderInfo">
                <van-image class="orderImg" lazy-load v-bind:src="orderInfo.firstGoodsPicUrl" />
                <div class="orderDetail">
                  <div style="display: flex;">
                    <div class="orderName userName">{{orderInfo.recName}} </div>
                    <div class="orderName" style="margin-left:10px;">{{orderInfo.recPhone}}</div>
                    <div class="orderStatus">{{orderInfo.billStatus}}</div>
                  </div>
                  <div class="orderName">{{orderInfo.recAddr}}</div>
                  <div class="orderName" style="color: #9a9a9a">{{orderInfo.createdTime}}</div>
                </div>
              </div>
              <div class="van-hairline--top lineMargin"></div>

              <div class="pricePanel" @click="gotoCommerceDetail(orderInfo)" v-if="orderInfo && orderInfo.goodsInfo && orderInfo.goodsInfo.length > 0">
                <div class="orderGoods">{{orderInfo.goodsInfo[0].goodsName}}</div>
                <div class="orderGoodsNum">共{{orderInfo.goodsInfo.length}}件商品</div>
                <div>
                  <span class="orderPrice">
                    <span style="font-size: 14px;">￥</span>
                    {{orderInfo.realPayTotal}}
                  </span>
                </div>
              </div>
              <div class="receivePanel" v-if="orderInfo.billStatus=='待收货' && orderInfo && orderInfo.goodsInfo && orderInfo.goodsInfo.length > 0">
                <div class="receiveBtn" @click="received(orderInfo)">已送达</div>
                <div class="receiveBtn" v-if="orderInfo.noticeTimes && orderInfo.noticeTimes <= 0" @click="goodArrive(orderInfo, index)">已接货</div>
                <div class="receiveBtn-disabled" v-else-if="orderInfo.noticeTimes && orderInfo.noticeTimes >= 1">已接货</div>
                <div class="receiveBtn" v-else @click="goodArrive(orderInfo, index)">已接货</div>
                <div class="receiveBtn" @click="getDadaExpressInfo(orderInfo)" v-if="dadaExpressSurported && orderInfo.expressCompany && (orderInfo.expressCompany.indexOf('达达') != -1)">查看物流</div>
              </div>
            </div>
          </van-list>
        </van-tab>
        <van-tab title="已完成">
          <div v-if="orderList.length <=0" class="noMessagePanel">
            <div class="noMessage">
              <img :src="imgDefault" style="display:block;width:2rem;margin: 0 auto;" />
              <p @click="gotoCommerceDetail">暂无订单</p>
            </div>
          </div>
          <van-list
            v-if="orderList.length >0"
            v-model="isLoading"
            :finished="finished"
            finished-text="没有更多了"
            @load="loadMore"
            :immediate-check="false"
          >
            <div class="card" v-for="(orderInfo,index) in orderList" :key="index">

              <div class="orderTitlePanel" @click="gotoCommerceDetail(orderInfo)" v-if="orderInfo">
                <div class="orderTitleStore">{{orderInfo.shopName}}</div>
                <div class="orderCode">订单号：{{orderInfo.subBillNo}}</div>
                <div v-if="orderInfo.deliveryType==0" class="SendTypeDelivery">
                  <div class="sendType">快递配送</div>
                </div>
                <div v-if="orderInfo.deliveryType==1" class="SendTypeDelivery">
                  <div class="sendType">销售配送</div>
                </div>
                <div v-if="orderInfo.deliveryType==2" class="SendTypeSelf">
                  <div class="sendType">到店自提</div>
                </div>
              </div>
              <div class="van-hairline--top" v-if="orderInfo"></div>

              <div class="orderInfoPanel" @click="gotoCommerceDetail(orderInfo)" v-if="orderInfo">
                <van-image class="orderImg" lazy-load v-bind:src="orderInfo.firstGoodsPicUrl" />
                <div class="orderDetail">
                  <div style="display: flex;">
                    <div class="orderName userName">{{orderInfo.recName}} </div>
                    <div class="orderName" style="margin-left:10px;">{{orderInfo.recPhone}}</div>
                    <div class="orderStatus">{{orderInfo.billStatus}}</div>
                  </div>
                  <div class="orderName">{{orderInfo.recAddr}}</div>
                  <div class="orderName" style="color: #9a9a9a">{{orderInfo.createdTime}}</div>
                </div>
              </div>
              <div class="van-hairline--top lineMargin"></div>

              <div class="pricePanel" @click="gotoCommerceDetail(orderInfo)" v-if="orderInfo && orderInfo.goodsInfo && orderInfo.goodsInfo.length > 0">
                <div class="orderGoods">{{orderInfo.goodsInfo[0].goodsName}}</div>
                <div class="orderGoodsNum">共{{orderInfo.goodsInfo.length}}件商品</div>
                <div>
                  <span class="orderPrice">
                    <span style="font-size: 14px;">￥</span>
                    {{orderInfo.realPayTotal}}
                  </span>
                </div>
              </div>
            </div>
          </van-list>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import {
  getOrderListService,
  sendGoodsArriveMsg,
  setOrderReceived,
  getDadaOrderTrack,
} from "../../services/commerceService.js";
import { store, storage } from "../../utils/store";
import eventBus from '../../utils/eventBus';
import log from "../../utils/log";
import NavigationBar from "../title/NavigationBar";
export default {
  name: "commerceOrderList",
  components: { NavigationBar },
  data() {
    return {
      orderList: [],
      isLoading: false,
      finished: false,
      orderData: { page: 1, row: 10, recAreaCode: "" },
      active: 0,
      imgDefault: require("../../assets/imgs/commerce/img_Default@2x.png"),
      serachIcon: require("../../assets/imgs/commerce/icon_Shape.png"),
      userInfo: null,
      scrollTop: 0,
      dadaExpressSurported: true,
    };
  },
  beforeRouteLeave(to, from, next) {
    this.scrollTop = document.querySelectorAll('.van-tab__pane')[this.active].scrollTop;
    next();
  },
  activated() {
    //设置当前tab页
    //获取用户数据
    this.userInfo = store.get("userInfo");
    this.userInfo = this.userInfo
      ? this.userInfo
      : storage.getStorage("custNews");
    this.orderData.recAreaCode = this.userInfo.AREA_CODE;
    if (JSON.stringify(this.$route.params) != "{}") {
      if(this.active != this.$route.params.orderInfo) {
        // 这里会执行onTabChange方法
        this.active = this.$route.params.orderInfo * 1;
      } else {
        this.initParams();
        this.getOrderList();
      }
      document.querySelectorAll('.van-tab__pane')[this.active].scrollTop = 0;
    } else {
      document.querySelectorAll('.van-tab__pane')[this.active].scrollTop = this.scrollTop;
    }
  },
  methods: {
    //初始化数据
    initParams() {
      this.orderData.page = 1;
      this.orderList = [];
      this.isLoading = false;
      this.finished = false;
    },
    //获取订单列表
    getOrderList() {
      let custNews = (store.get("custNews") && store.get("custNews").AREA_CODE) ?
              store.get("custNews") : storage.getStorage("custNews");
      if (this.userInfo.AREA_CODE == "" || this.userInfo.AREA_CODE == undefined) {
        log.i("area code empty, reset first.");
        this.userInfo = (custNews && custNews.AREA_CODE) ? custNews : storage.getStorage("custNews");
        this.orderData.recAreaCode = this.userInfo.AREA_CODE;
      }
      if (this.userInfo.AREA_CODE != custNews.AREA_CODE) {
        log.i("area code changed, reset first.");
        this.userInfo = (custNews && custNews.AREA_CODE) ? custNews : storage.getStorage("custNews");
        this.orderData.recAreaCode = this.userInfo.AREA_CODE;
      }
      this.orderData.orderCate = this.active;
      this.isLoading = true;
      getOrderListService(this.orderData)
        .then(result => {
          //请求成功
          if (result.resultCode == "0") {
              //最后一页，结束分页加载
              if (result.pages <= this.orderData.page || result.pages == 0) {
                this.finished = true;
              } else {
                this.orderData.page++;
                this.finished = false;
              }
              setTimeout(() => {
                //返回值循环加入数组中，只有push才能vue识别
                this.orderList = this.orderList.concat(result.data);
              }, 200);
          } else {
            //加载失败后，关闭加载等待的文字
            result.msg
                  ? this.$toast(result.msg)
                  : this.showMsg("网络异常，请稍后重试！");
            this.finished = true;
          }
          this.isLoading = false;
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("加载失败，请稍后重试！");
          this.isLoading = false;
          this.finished = true;
        });
    },
    loadMore() {
        this.getOrderList();
    },
    //订单收货按钮事件
    received(orderInfo) {
      var data = {
        subBillNo: orderInfo.subBillNo,
        signerType: "03",
        signerCode: this.userInfo.USER_CODE
      };
      setOrderReceived(data)
        .then(result => {
          //请求成功
          if (result.resultCode == "0") {
            this.changeDataFromList(orderInfo.subBillNo);
          } else {
            result.msg
                  ? this.$toast(result.msg):this.$toast("请求失败，请稍后重试！");
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    changeDataFromList(subBillNo) {
      var index = -1;
      for(var i = 0; i < this.orderList.length; i++) {
        if(this.orderList[i].subBillNo == subBillNo) {
          index = i;
          break;
        }
      }
      if(this.active == 0) {   // 全部
        this.orderList[index].billStatus = "已完成";
      } else if (this.active == 1) {
        this.orderList.splice(index, 1);
      }
      // 提示首页自动刷新
      eventBus.$emit('reloadSaleNum');
    },
    //到货通知按钮事件
    goodArrive(orderInfo, index) {
      if (this.dadaExpressSurported && orderInfo.noticeTimes && orderInfo.noticeTimes >= 1) {
        console.log("goods already arrived, need not request, ignore.");
        return;
      }
      console.log(JSON.stringify(this.userInfo));
      var data = {
        subBillNo: orderInfo.subBillNo,
        userPhone: this.userInfo.PHONE
      };
      sendGoodsArriveMsg(data)
        .then(result => {
          //请求成功
          if (result.resultCode == "0") {
            this.$toast("发送成功");
            // 更新对应的item为已到货状态
            orderInfo.noticeTimes = 1;
            this.$set(this.orderList, index, orderInfo);
          } else {
            result.msg
                ? this.$toast(result.msg):this.$toast("请求失败，请稍后重试！");
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    gotoCommerceDetail(orderInfo) {
      store.set("dealFunction", this.changeDataFromList);
      this.$router.push({
        path: "/commerceOrderDetail",
        query: {
          orderInfo: orderInfo
        }
      });
    },
    gotoSearch() {
      store.set("dealFunction", this.changeDataFromList);
      this.$router.push({
        path: "/commerceSearch",
        query: {
          orderType: 0
        }
      });
    },
    onClickLeft() {
      store.dele("dealFunction");
      this.$router.back();
    },
    onTabChange(index) {
      this.initParams();
      this.getOrderList();
    },
    getDadaExpressInfo(orderInfo) {
      this.$router.push({ name: "mapForExpress", params: { subBillNo: orderInfo.subBillNo } });
        // getDadaOrderTrack(orderInfo.subBillNo)
        //   .then(result => {
        //     console.log("the track result " + result);
        //     // if is dada express
        //     // show dada track url in new page
        //     this.$toast("the track result " + JSON.stringify(result));
        //   })
        //   .catch(err => {
        //     console.log("get dada order info exception: " + err);
        //     this.$toast(err);
        //   })
    },
  }
};
</script>

<style>
.commerceOrderList {
  background-color: #f2f2f2;
  height: 100%;
}

/* 列表搜索 */
.searchPanel {
  height: 44px;
  width: 100%;
  background-color: #00d8d1;
  position: fixed;
  top: 43px;
  left: 0;
  z-index: 99;
}

.searchPanel .searchInput {
  height: 33px;
  margin: 0 20px;
  background-color: white;
  color: #9a9a9a;
  font-size: 13px;
  border-radius: 2px;
  text-align: center;
}

.searchPanel .searchInput .searchImg {
  height: 11px;
  width: 11px;
  margin-top: 11px;
}

.card {
  padding-top: 1px;
  padding-bottom: 1px;
  box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.5);
  border-radius: 5px;
  overflow: hidden;
  margin: 10px 10px;
  background-color: #fff;
}

/* 店铺名+订单号 */
.commerceOrderList .orderTitlePanel {
  height: 50px;
  line-height: 50px;
  padding: 0 15px;
  position: relative;
}

/* 重新tab和tabs样式 */
.commerce-tabs-box {
  position: fixed;
  top: 90px;
  width: 100%;
  z-index: 90;
  bottom: 0;
}
.commerce-tabs-box .van-tabs {
  height: 100%!important;
  background: #ffffff;
}
.commerce-tabs-box .van-tabs .van-tabs__wrap {
  border: 0 !important;
}
.commerce-tabs-box .van-hairline--top-bottom::after, .commerce-tabs-box .van-hairline-unset--top-bottom::after {
  border: 0 !important;
}
.commerce-tabs-box .van-tabs__content {
  position: absolute;
  top: 38px;
  bottom: 0;
  box-sizing: border-box;
  width: 100%;
}
.commerce-tabs-box .van-tabs__content .van-tab__pane {
  height: 100%;
  overflow: auto;
  background-color: #f2f2f2;
}
.commerce-tabs-box .van-tabs .van-tabs__nav {
  height: 38px !important;
  background: transparent !important;
}
.commerce-tabs-box .van-tab {
  height: 38px !important;
}
.commerceOrderList .orderTitlePanel .orderTitleStore {
  font-size: 14px;
  color: #333333;
  letter-spacing: -0.04px;
  font-weight: bold;
  height: 20px;
  line-height: 20px;
  margin-top: 10px;
  text-align: left;
}

.commerceOrderList .orderTitlePanel .orderCode {
  font-size: 12px;
  color: #4a4a4a;
  letter-spacing: -0.04px;
  height: 20px;
  line-height: 20px;
  text-align: left;
}

/* 商品图片+详情 */
.commerceOrderList .orderInfoPanel {
  /* height: 100px; */
  display: flex;
}

.commerceOrderList .orderInfoPanel .orderImg {
  height: 65px;
  width: 65px;
  min-width: 65px;
  margin-top: 15px;
  margin-left: 15px;
  border-radius: 2px;
}

.commerceOrderList .orderInfoPanel .orderDetail {
  margin: 18px 12px 10px 12px;
  width: 100%;
  text-align: left;
}

.commerceOrderList .orderInfoPanel .orderDetail .orderName {
  line-height: 20px;
  font-size: 12px;
}
.commerceOrderList .orderInfoPanel .orderDetail .userName {
  max-width: 60px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.commerceOrderList .orderInfoPanel .orderStatus {
  float: right;
  font-size: 12px;
  color: #ff5050;
  flex-grow: 1;
  text-align: right;
  min-width: 50px;
