  width: 50%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: left;
}

.commerceOrderList .pricePanel .orderGoodsNum {
  font-size: 14px;
  color: #9a9a9a;
  letter-spacing: -0.04px;
  margin-left: 15px;
  width: 20%;
