  margin-right: 15px;
  font-size: 16px;
  color: #f43d3d;
  text-align: left;
}

/* 收货按钮 */
.commerceOrderList .receivePanel {
  height: 45px;
}

.commerceOrderList .receivePanel .receiveBtn {
  background: #00d8d1;
  border: 1px solid #00d8d1;
  box-shadow: 0 0 8px 0 rgba(0, 158, 161, 0.32);
  border-radius: 14.5px;
  height: 30px;
  line-height: 30px;
  width: 92px;
  color: white;
  text-align: center;
  float: right;
  margin-right: 15px;
  font-size: 14px;
}

.commerceOrderList .receivePanel .receiveBtn-disabled {
  background: #888;
  border: 1px solid #888;
  border-radius: 14.5px;
  height: 30px;
  line-height: 30px;
  width: 92px;
  color: white;
  text-align: center;
  float: right;
  margin-right: 15px;
  font-size: 14px;
}

/* 发货方式tip */
.commerceOrderList .SendTypeSelf {
  position: absolute;
  right: 0;
  top: -11px;
  height: 40px;
  width: 40px;
  background-color: #f5a623;
  border-radius: 0 5px;
}

.commerceOrderList .SendTypeDelivery {
  position: absolute;
  right: 0;
  top: -11px;
  height: 40px;
  width: 40px;
  background-color: #8ab7ff;
  border-radius: 0 5px;
}

.commerceOrderList .sendType {
  font-size: 10px;
  line-height: 12px;
  width: 30px;
  height: 30px;
  text-align: center;
  margin: 10px 5px 0 5px;
  color: white;
}

.commerceOrderList .lineMargin {
  margin: 10px 15px;
}
.commerceOrderList .noMessage {
  margin: 0 auto;
  text-align: center;
  color: #ddd;
  font-size: 13px;
}
.commerceOrderList .noMessagePanel {
  width: 100%;
  height: 100%;
  padding-top: 6rem;
  box-sizing: border-box;
}
.commerceOrderList .noMessageImg {
  display: block;
  width: 2rem;
  margin: 0 auto;
}

